@use 'config';
@use 'grid';

%square {
  &::after {
    content: '';
    display: block;
    padding-top: 100%;
  }
}

.nc-skeleton {
  $this: &;

  grid-column: 1 / span 12;

  & + & {
    margin-top: config.sp-vertical(3);
  }

  display: grid;
  grid-column-gap: config.$gutter--default;
  grid-row-gap: config.sp-vertical(2);
  grid-template-rows: config.sp-vertical(3);

  &__item {
    animation: {
      direction: normal;
      duration: 3s;
      fill-mode: forwards;
      iteration-count: infinite;
      name: skeleton-item;
      play-state: running;
      timing-function: ease;
    }
    background: linear-gradient(
      90deg,
      rgba(config.$nc-c-secondary--disabled, 0.2) 0%,
      rgba(config.$nc-c-secondary--disabled, 0.5) 70%,
      rgba(config.$nc-c-secondary--disabled, 0.1) 100%
    );
    background-size: 200%;
    min-height: config.sp-vertical(3);
  }

  &--default {
    grid-template: 'item';
  }

  &--stage {
    grid-template: 'item';
    height: 58px;
  }

  &--productList {
    grid-template:
      'image  head  head  head'
      'image  copy  copy  .'
      'image  .     .     .'
      'image  .     .     .';

    .image {
      @extend %square;

      grid-area: image;
    }
    .head {
      grid-area: head;
    }
    .copy {
      grid-area: copy;
    }
  }

  &--productDetail {
    grid-template:
      'image  image . copy  copy  copy'
      'image  image . copy  copy  copy'
      'image  image . copy  copy  copy'
      'image  image . .     .     .'
      'thumb  .     . .     .     .'
      'thumb  .     . .     .     .';

    .image {
      @extend %square;

      grid-area: image;
    }
    .copy {
      grid-column: 4 / span 3;

      &div:nth-of-type(1) {
        grid-row: 1 / span 1;
      }
      &div:nth-of-type(2) {
        grid-row: 2 / span 1;
      }
      &div:nth-of-type(3) {
        grid-row: 3 / span 1;
      }
    }
    .thumb {
      grid-area: thumb;
    }
  }

  &--checkout {
    grid-template:
      'image  head  head head . summary summary'
      'image  copy  copy .    . summary summary'
      'image  copy  copy .    . summary summary'
      'image  .     .    .    . summary summary'
      '.      .     .    .    . summary summary';

    .image {
      @extend %square;

      grid-area: image;
    }
    .head {
      grid-area: head;
    }
    .copy {
      grid-column: 2 / span 2;

      &div:nth-of-type(1) {
        grid-row: 2 / span 1;
      }
      &div:nth-of-type(2) {
        grid-row: 3 / span 1;
      }
    }
    .summary {
      grid-area: summary;
      height: config.sp(30);
    }
  }
}

@keyframes skeleton-item {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
