@use 'config';

.nc-iconButton {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: config.$transition-basic;
  transition-property: color;

  &:hover {
    color: config.$nc-c-primary--hover;
  }

  &--isDisabled {
    color: config.$nc-c-primary--disabled-contrast;
    pointer-events: none;
  }
}
