@use 'config';
@use 'utility';

.nc-gallery {
  @include utility.breakpoint(config.$bp-desktop) {
    display: grid;
    grid-column-gap: config.$gutter--default;
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: 'thumbnails display display display';
  }

  &--no-thumbnails {
    grid-template-areas: 'display display display display';

    .nc-gallery__display {
      @include utility.breakpoint(config.$bp-mobile-large) {
        max-width: config.sp(52 + 15);
      }
    }
  }

  &__display {
    align-items: flex-start;
    display: flex;
    justify-content: flex-start;
    position: relative;

    @include utility.breakpoint(config.$bp-mobile-large) {
      max-width: config.sp(52);
    }

    @include utility.breakpoint(config.$bp-desktop) {
      grid-area: display;
    }
  }

  &__thumbnails {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: config.$gutter--default;
    margin-top: config.sp-vertical(3);

    @include utility.breakpoint(config.$bp-desktop) {
      gap: config.$gutter--default;
      display: flex;
      flex-direction: column;
      grid-area: thumbnails;
      margin-top: 0;
    }
  }

  &__thumbnail {
    cursor: pointer;

    @include utility.breakpoint(config.$bp-mobile-large) {
      max-width: config.sp(15);
    }

    &--isActive {
      outline-offset: -1px;
      outline: solid 1px config.$nc-c-primary;
    }

    & .nc-image__img {
      aspect-ratio: 1;
      object-fit: cover;
    }
  }

  &__control {
    bottom: 0;
    cursor: pointer;
    position: absolute;
    top: 0;

    &--prev {
      left: 0;
      right: 75%;
    }

    &--next {
      left: 75%;
      right: 0;
    }
  }
}
