@use 'config';
@use 'utility';

.nc-tabs {
  &__header {
    display: flex;
    border-bottom: solid 1px config.$nc-c-divider;
  }

  &__button {
    @include utility.getFont(config.$f-text-button);

    align-items: center;
    cursor: pointer;
    display: flex;
    flex: 1 0 auto;
    justify-content: center;
    min-height: config.sp(7);
    padding: 0 config.sp(3);
    position: relative;
    width: fit-content;

    @include utility.breakpoint(config.$bp-mobile-large) {
      flex: 0 0 auto;
    }

    &--isActive {
      &::after {
        display: block;
        content: '';
        height: config.sp(0.25);
        background-color: config.$nc-c-text;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
      }
    }

    &--isDisabled {
      pointer-events: none;
      cursor: default;
      color: config.$nc-c-primary--disabled-contrast;
    }
  }

  &__item {
    padding: config.sp-vertical(3) 0 config.sp-vertical(4);
    z-index: 1;
  }
}
