@use 'config';
@use 'utility';
@use 'sass:math' as math;

$checkout-stepper-step-width: 25%;
$checkout-stepper-icon-size: config.sp(5);

.nc-checkoutStepper {
  align-items: flex-start;
  display: flex;
  justify-content: space-between;
  margin-bottom: config.sp-vertical(5);
  margin-top: config.sp-vertical(6);
  position: relative;

  &::after {
    background-color: config.$nc-c-primary;
    bottom: 50%;
    content: '';
    display: block;
    height: 1px;
    left: math.div($checkout-stepper-step-width, 2);
    position: absolute;
    right: math.div($checkout-stepper-step-width, 2);
    top: math.div($checkout-stepper-icon-size, 2);
    z-index: 1;
  }

  &--skeleton {
    > * {
      flex-grow: 1;
    }

    &::after {
      display: none;
    }
  }

  &__step {
    $step: &;

    align-items: center;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    width: $checkout-stepper-step-width;
    z-index: 2;

    &--isConfirmed {
      color: config.$nc-c-primary;
    }

    &.active {
      color: config.$nc-c-primary--hover;
    }

    &__icon {
      @include utility.getFont(config.$f-stepperLabel);

      align-items: center;
      background-color: config.$nc-c-white;
      border-radius: 100%;
      border: solid 1px config.$nc-c-primary;
      display: flex;
      height: $checkout-stepper-icon-size;
      justify-content: center;
      user-select: none;
      width: $checkout-stepper-icon-size;
      transition: background-color config.$transition-snappy,
        color config.$transition-snappy;

      #{$step}.active & {
        background-color: config.$nc-c-primary--hover;
        border: none;
        color: config.$nc-c-background;
      }
      #{$step}--isConfirmed & {
        background-color: config.$nc-c-primary;
        border: none;
        color: config.$nc-c-background;
        padding: 5px;
      }
    }

    &__label {
      margin-top: config.sp(2);
      text-align: center;

      #{$step}.active & {
        font-weight: 700;
      }
    }
  }
}
