@use 'config';
@use 'utility';

.nc-wallet {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  &__checkout {
    margin-bottom: config.$gutter--default;
    margin-top: config.$gutter--default;
  }

  &__list {
    margin-top: config.$gutter--default;

    &__control {
      min-height: config.sp(7);
    }

    &__item {
      display: flex;
      gap: config.sp(1);
      font-weight: 700;
    }

    &__quantity {
      align-items: center;
      display: flex;
      justify-content: center;
      position: relative;

      &__count {
        align-items: center;
        display: flex;
        font-size: 8px;
        inset: config.sp(0.5) 0 0;
        justify-content: center;
        position: absolute;
      }
    }
  }
}
