@use 'config';
@use 'utility';

.nc-image {
  position: relative;

  &--withOverlay {
    &::after {
      background-image: linear-gradient(
        to bottom,
        config.$nc-c-transparent,
        config.$nc-c-primary
      );
      content: '';
      inset: 0;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      transition: config.$transition-long;
      transition-property: opacity;
    }

    &:hover {
      &::after {
        opacity: 0.6;
      }
    }
  }

  &__img {
    display: block;
  }
}
