@use 'config';
@use 'utility';
@use 'common';

.nc-select {
  $this: &;

  @include utility.getFont(config.$f-formControlInput);

  &--hasError {
    color: config.$nc-c-error;
  }

  &__input {
    position: absolute;
    width: 0;
    height: 0;
    pointer-events: none;
  }

  &__control {
    position: relative;
  }

  &__toggle {
    @extend %form-control-appearance;

    align-items: center;
    cursor: pointer;
    display: flex;
    width: 100%;
    gap: config.$gutter--default;

    &__icon {
      margin-left: auto;
      pointer-events: none;

      #{$this}--isOpen & {
        transform: scaleY(-1);
      }
    }

    &__label {
      @include utility.getFont(config.$f-formControlInput);

      flex: 0 1 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  &__search {
    &::placeholder {
      color: config.$nc-c-primary--disabled-contrast;
    }
  }

  &__options {
    color: config.$nc-c-primary;
    left: 0;
    max-height: config.sp(6 * 5); // hopefully 5 rows
    overflow-y: auto;
    position: absolute;
    top: calc(100% + 1px);
    width: 100%;
    z-index: 1;
  }

  &__option {
    align-items: center;
    cursor: pointer;
    display: flex;
    min-height: config.sp(6);
    padding: config.sp(1) config.sp(2.5);
    width: 100%;

    &:hover {
      background-color: config.$nc-c-primary--hover;
      color: config.$nc-c-white;
    }

    &--isSelected {
      background-color: config.$nc-c-primary;
      color: config.$nc-c-white;
    }

    &--isDisabled {
      color: config.$nc-c-primary--disabled-contrast;
      cursor: default;
      pointer-events: none;
    }
  }
}
