// colors

// -- common
$nc-c-white: var(--nc-white, rgb(255, 255, 255));
$nc-c-black: var(--nc-black, rgb(0, 0, 0));
$nc-c-grey: rgba(216, 216, 216, 1);
$nc-c-transparent: rgba(0, 0, 0, 0);

// -- primary
$nc-c-primary: rgba(46, 48, 142, 1);
$nc-c-primary--contrast: $nc-c-white;
$nc-c-primary--hover: rgba(74, 77, 240, 1);
$nc-c-primary--disabled: rgba(213, 214, 232, 1);
$nc-c-primary--disabled-contrast: rgba(150, 151, 198, 1);

// -- secondary
$nc-c-secondary: rgba(115, 115, 216, 1);
$nc-c-secondary--contrast: $nc-c-white;
$nc-c-secondary--hover: rgba(133, 160, 249, 1);
$nc-c-secondary--disabled: $nc-c-primary--disabled;
$nc-c-secondary--disabled-contrast: $nc-c-primary--disabled-contrast;

// -- states
$nc-c-error: var(--nc-error, rgba(255, 106, 106, 1));
$nc-c-error-contrast: var(--nc-error, rgba(255, 215, 215, 1));

$nc-c-success: var(--nc-error, rgba(85, 220, 147, 1));
$nc-c-success-contrast: var(--nc-error, rgba(208, 246, 226, 1));

$nc-c-warning: var(--nc-error, rgba(255, 213, 106, 1));
$nc-c-warning-contrast: var(--nc-error, rgba(255, 245, 210, 1));

// -- context
$nc-c-background: $nc-c-white;
$nc-c-overlay: rgba(0, 0, 0, 0.2);
$nc-c-text: $nc-c-primary;
$nc-c-divider: $nc-c-primary--disabled;
