@use 'config';
@use 'utility';

.nc-cartItem {
  $this: &;

  display: grid;
  grid-column-gap: config.$gutter--default;
  grid-row-gap: 0;
  grid-template-columns: repeat(3, 1fr);

  @include utility.breakpoint(config.$bp-mobile-large) {
    grid-template-columns: repeat(4, 1fr);
  }

  & + & {
    margin-top: config.sp-vertical(3);
    position: relative;

    &::after {
      background-color: config.$nc-c-divider;
      content: '';
      height: 1px;
      left: config.sp-vertical(2);
      position: absolute;
      top: config.sp-vertical(3) * -1;
      width: calc(100% - #{config.sp-vertical(2) + config.sp-vertical(3)});
    }
  }

  &__aside {
    align-items: center;
    display: flex;
    flex-direction: column;
    grid-column: 1 / span 1;
    grid-row: 2;
    justify-content: space-between;
    padding: config.sp-vertical(2) config.sp-vertical(2) config.sp-vertical(3)
      config.sp-vertical(2);

    @include utility.breakpoint(config.$bp-mobile-small) {
      grid-row: 1;
    }
  }

  &__main {
    grid-column: 1 / span 3;
    padding: config.sp-vertical(3) config.sp-vertical(3) config.sp-vertical(3)
      config.sp-vertical(2);

    @include utility.breakpoint(config.$bp-mobile-small) {
      grid-column: 2 / span 2;
      padding-left: 0;
    }

    @include utility.breakpoint(config.$bp-mobile-large) {
      align-items: flex-start;
      display: flex;
      gap: config.$gutter--default;
      grid-column: 2 / span 3;
      justify-content: space-between;
    }
  }

  &__action {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    justify-self: left;

    @include utility.breakpoint(config.$bp-mobile-large) {
      padding-bottom: config.sp-vertical(1);
      padding-top: config.sp-vertical(1);
    }
  }

  &__remove {
    margin-top: config.sp-vertical(2);
    // margin-left: config.sp-vertical(1);
  }

  &__description {
    @include utility.breakpoint(config.$bp-mobile-large) {
      flex: 1 0 auto;
      max-width: 60%;

      #{$this}--hiddenActions & {
        max-width: none;
      }
    }

    &__price {
      margin-top: config.sp-vertical(2);
    }

    &__options {
      margin-top: config.sp-vertical(2);
    }

    &__quantity {
      margin-top: config.sp-vertical(3);
    }
  }

  &__quantity {
    flex: 0 1 auto;
    margin-top: config.sp-vertical(3);

    @include utility.breakpoint(config.$bp-mobile-large) {
      flex: 1 0 auto;
      margin-top: 0;
      max-width: 30%;
    }
  }
}
