@use 'config';
@use 'utility';

.nc-avatar {
  @include utility.getFont(config.$f-avatar);

  align-items: center;
  background-color: config.$nc-c-primary;
  border-radius: 100%;
  border: solid config.sp(0.5) config.$nc-c-primary;
  color: config.$nc-c-primary--contrast;
  cursor: pointer;
  display: flex;
  height: config.sp(3);
  justify-content: center;
  transition: config.$transition-basic;
  transition-property: border-color, background-color;
  width: config.sp(3);

  &:hover {
    border-color: config.$nc-c-primary--hover;
    background-color: config.$nc-c-primary--hover;
  }

  @include utility.breakpoint(config.$bp-mobile-large) {
    height: config.sp(6);
    width: config.sp(6);
  }
}
