@use 'config';
@use 'utility';

.nc-modal {
  display: none;

  &--isOpen {
    align-items: center;
    background-color: config.$nc-c-overlay;
    display: flex;
    inset: 0;
    justify-content: center;
    position: fixed;
    z-index: config.z-index(modal);
  }

  &__content {
    background-color: config.$nc-c-background;
    box-shadow: config.$nc-shadow-small;
    padding: config.sp(6);
    position: relative;
    margin: 0 config.sp(2.5);

    @include utility.breakpoint(config.$bp-mobile-large) {
      max-width: 66%;
    }
  }

  &__close-icon {
    position: absolute;
    top: config.sp(3);
    right: config.sp(3);
  }
}
