@use 'config';
@use 'utility';
@use 'common';

.nc-button {
  $this: &;

  @include utility.getFont(config.$f-button);

  @extend %focus-visible;
  @extend %button-appearance;

  &--primary {
    border-color: config.$nc-c-primary;
    background-color: config.$nc-c-primary;
    color: config.$nc-c-primary--contrast;

    &:hover {
      border-color: config.$nc-c-primary--hover;
      background-color: config.$nc-c-primary--hover;
    }
  }

  &--secondary {
    background-color: config.$nc-c-white;
    color: config.$nc-c-primary;

    &:hover {
      border-color: config.$nc-c-primary--hover;
      color: config.$nc-c-primary--hover;
    }
  }

  &--isDisabled {
    background-color: config.$nc-c-primary--disabled;
    border-color: config.$nc-c-primary--disabled;
    color: config.$nc-c-primary--disabled-contrast;
    pointer-events: none;
  }

  &--text {
    border: none;
    border-radius: 0;
    color: config.$nc-c-secondary;
    min-height: config.sp(3);
    padding: 0;

    &:hover {
      color: config.$nc-c-secondary--hover;
    }

    &#{$this}--isDisabled {
      background-color: transparent;
      color: config.$nc-c-secondary--disabled;
      pointer-events: none;
    }
  }

  &--isFullWidth {
    width: 100%;
  }

  &--icon:not(#{$this}--text) {
    padding-left: config.sp(0.5);
  }

  &__icon {
    align-items: center;
    background-color: config.$nc-c-white;
    border-radius: 100%;
    display: flex;
    flex-shrink: 0;
    height: config.sp(4);
    justify-content: space-around;
    margin-right: config.sp(1.5);
    width: config.sp(4);

    #{$this}--primary & {
      color: config.$nc-c-primary;
    }

    #{$this}--primary:hover & {
      color: config.$nc-c-primary--hover;
    }

    #{$this}--isDisabled & {
      color: config.$nc-c-primary--disabled-contrast;
    }

    #{$this}--text & {
      height: config.sp(3);
      width: config.sp(3);
    }
    #{$this}--text#{$this}--isDisabled & {
      color: config.$nc-c-primary--disabled;
    }
  }

  &__content {
    align-items: center;
    display: flex;
    flex: 1 1 auto;
    justify-content: center;
    max-width: 100%;
  }
}
