@use 'styles/config';
@use 'styles/utility';
@use 'styles/common';

.aem-header {
  box-shadow: config.$nc-shadow-top-bar;
  height: config.sp(6.5);

  @include utility.breakpoint(config.$bp-mobile-large) {
    height: config.sp(12);
  }

  &__content {
    @extend %container;

    align-items: center;
    display: flex;
    gap: config.$gutter--default;
    height: 100%;
    justify-content: space-between;
  }
}
