@use 'config';

.nc-accordion {
  $this: &;

  border: solid 1px config.$nc-c-secondary;
  padding: 0 config.sp(4);

  + .nc-accordion {
    border-top: none;
  }

  &__header {
    align-items: center;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    min-height: 78px;
  }

  &__label {
    color: config.$nc-c-text;
  }

  &__icon {
    transition: transform config.$transition-basic;

    #{$this}--isOpen & {
      transform: rotate(-180deg);
    }
  }

  &__content {
    height: 0;
    overflow: hidden;
    transition: height config.$transition-basic;

    &__inner {
      padding: config.sp(1) 0 config.sp(4);
    }
  }
}
