@use 'config';
@use 'utility';

.nc-widget {
  align-items: center;
  display: flex;
  gap: config.$gutter--default;

  &__header {
    border-bottom: solid 1px config.$nc-c-primary;
    margin-bottom: config.$gutter--default;
    padding-bottom: config.$gutter--default;
  }

  &__cart {
    position: relative;
  }

  &__item {
    flex-shrink: 0;
    height: config.sp(3);
    position: relative;
    width: config.sp(3);

    @include utility.breakpoint(config.$bp-mobile-large) {
      height: config.sp(6);
      width: config.sp(6);
    }
  }

  &__menu {
    padding: config.sp(3);
    flex-shrink: 0;
    margin-top: config.sp(1);
    width: config.sp(50);
  }

  &__customer {
    @include utility.getFont(config.$f-link);

    border-bottom: solid 1px config.$nc-c-primary;
    padding-bottom: config.sp-vertical(3);
  }

  &__link {
    @include utility.getFont(config.$f-link);

    align-items: center;
    display: flex;
    font-weight: 700;
    min-height: config.sp(7);
    text-decoration: none;

    & + & {
      border-top: solid 1px config.$nc-c-divider;
    }

    &:hover {
      color: config.$nc-c-primary--hover;
    }
  }

  &__counter {
    @include utility.getFont(config.$f-widgetCount);

    align-items: center;
    background-color: currentColor;
    border-radius: config.sp(0.75);
    bottom: config.sp(0);
    display: flex;
    height: config.sp(1.5);
    justify-content: center;
    min-width: config.sp(1.5);
    padding: config.sp(0.125);
    position: absolute;
    right: config.sp(0);

    @include utility.breakpoint(config.$bp-mobile-large) {
      border-radius: config.sp(1.25);
      bottom: config.sp(0.5);
      height: config.sp(2.5);
      min-width: config.sp(2.5);
      padding: config.sp(0.25);
      right: config.sp(0.5);
    }

    &__label {
      color: config.$nc-c-background;
    }
  }
}
