@use 'config';
@use 'utility';

%container {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 config.sp(2.5);

  @include utility.breakpoint(config.$bp-mobile-large) {
    padding: 0 config.sp(5);
  }

  @include utility.breakpoint(config.$bp-desktop) {
    padding: 0 config.sp(10);
  }
}

%dense-box {
  @include utility.breakpoint(config.$bp-mobile-large) {
    padding-right: 35%;
  }
}

%box-padding--default {
  padding: config.sp-vertical(3) config.sp(2.5);
}
%box-padding--card {
  padding: config.sp-vertical(3) config.sp(2.5) config.sp-vertical(3);
}

%button-appearance {
  @include utility.getFont(config.$f-button);

  border-radius: config.sp(2.5);
  border-style: solid;
  border-width: 1px;
  cursor: pointer;
  // if ie11 must be supported layout relevant styles need to be moved to a content wrapper (no button flex)
  align-items: center;
  display: inline-flex;
  justify-content: center;
  min-height: config.sp(5);
  min-width: fit-content;
  padding: config.sp(0.5) config.sp(3);
  text-align: center;
  text-decoration: none;
  transition: config.$transition-basic;
  transition-property: border-color, background-color, color;
}

%focus-visible {
  &:focus-visible {
    box-shadow: config.$nc-shadow--focus;
  }
}

%form-control-appearance {
  @extend %focus-visible;

  appearance: none;
  border-radius: 3px;
  border: 1px solid currentColor;
  min-height: config.sp(6);
  min-width: config.sp(10);
  padding: config.sp(0.5) config.sp(2.5);
  transition: config.$transition-snappy;
  transition-property: border-color, color;

  &:hover,
  &--isOpen,
  &--isActive {
    border-color: config.$nc-c-primary--hover;
    color: config.$nc-c-primary--hover;
  }
}
