@use 'config';
@use 'common';
@use 'utility';

.nc-footer {
  width: 100%;

  &__top-bg {
    width: 100%;
    background-color: config.$nc-c-primary;
  }

  &__top {
    @extend %container;
    padding-top: config.sp-vertical(4);
    padding-bottom: config.sp-vertical(5);
    color: config.$nc-c-primary--contrast;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    a {
      text-decoration: none;
    }
  }

  &__logo {
    display: flex;
    height: config.sp(8.5);
    width: 250px;
    padding: config.sp(1);
  }

  &__main-menu {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: config.sp(5);
    padding-top: config.sp-vertical(3);

    @include utility.breakpoint(config.$bp-desktop) {
      flex-direction: row;
      gap: config.sp(2);
    }
  }

  &__column {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  &__socials {
    display: flex;
    gap: config.sp(3);
    padding: config.sp(1);
  }

  &__links {
    margin-top: config.sp-vertical(3);
    display: flex;
    flex-direction: column;
    gap: config.sp(1);
  }

  &__bottom {
    @extend %container;
    display: flex;
    flex-direction: column;
    gap: config.sp-vertical(5);
    padding-top: config.sp-vertical(3);
    padding-bottom: config.sp-vertical(5);
  }

  &__bottom-menu {
    display: flex;
    flex-direction: column;
    gap: config.sp(2);

    @include utility.breakpoint(config.$bp-desktop) {
      flex-direction: row;
      gap: config.sp(4);
    }
  }

  &__bottom-menu-item {
    width: max-content;
  }

  &__disclaimer {
    @include utility.getFont(config.$f-disclaimer);
  }
}
