@forward '../../node_modules/breakpoint-sass';
@use '../../node_modules/breakpoint-sass' as *;

@use 'sass:math';
@use 'config';

@function pxToRem($value) {
  @return math.div($value, config.$nc-base-font-size) + rem;
}

@mixin getFontDefinition($set) {
  @each $property, $value in $set {
    @if ($property == 'font-size') {
      $value: pxToRem($value);
    }
    #{$property}: $value;
  }
}

@mixin getFont($fontDefinition) {
  @each $breakpoint, $set in $fontDefinition {
    @if $breakpoint == default {
      @include getFontDefinition($set);
    } @else {
      @include breakpoint($breakpoint) {
        @include getFontDefinition($set);
      }
    }
  }
}
