@use 'config';
@use 'utility';

.nc-productOptionSelector {
  // TODO establish form control abstraction
  &__control {
    display: flex;
    align-items: center;
    justify-content: space-between;

    & + & {
      margin-top: config.sp(1);
    }

    &__label {
      @include utility.getFont(config.$f-formControlLabelHorizontal);
    }

    &__element {
      flex: 0 0 config.sp(20);
      max-width: config.sp(20);
    }
  }
}
