@use 'config.colors';

@forward 'config.breakpoints';
@forward 'config.typography';
@forward 'config.colors';

// spacing
$sp: 8px;
@function sp($factor: 1) {
  @return $sp * $factor;
}
$vertical-space-map: (
  1: sp(1),
  2: sp(1.5),
  3: sp(3),
  4: sp(4.5),
  5: sp(6),
  6: sp(8),
);
@function sp-vertical($key: 1) {
  @if (map-has-key($vertical-space-map, $key)) {
    @return map-get($vertical-space-map, $key);
  } @else {
    @error 'The spacing index "#{$key}" can not be found';
  }
}

$gutter--default: sp(3);

// z-index
$z-index-map: (
  menu: 1000,
  modal: 1100,
  notification: 1200,
);
@function z-index($key) {
  @if (map-has-key($z-index-map, $key)) {
    @return map-get($z-index-map, $key);
  } @else {
    @error 'The z-index "#{$key}" can not be found';
  }
}

// shadows
$nc-shadow-color-default: config.$nc-c-secondary;
$nc-shadow-color-default--hover: config.$nc-c-secondary--hover;

$nc-shadow-small: 0 0 sp(1) rgba($nc-shadow-color-default, 0.5);
$nc-shadow-small--hover: 0 0 sp(1) rgba($nc-shadow-color-default--hover, 0.5);

$nc-shadow-focus-color: config.$nc-c-primary--hover;
$nc-shadow--focus: 0 0 0 sp(0.5) rgba($nc-shadow-focus-color, 0.35);

$nc-shadow-top-bar: 0 sp(0.5) sp(0.5) rgba(config.$nc-c-primary, 0.25);

// transition
$transition-long: 0.66s ease-in-out;
$transition-basic: 0.36s ease;
$transition-snappy: 0.15s ease;
