@use 'config';
@use 'utility';

.nc-addToCartConfirmationDialog {
  &__modal {
    display: flex;
    flex-direction: column;
  }

  &__successText {
    align-items: center;
    display: flex;
    gap: config.sp(2);
    margin-top: config.sp(5);
    margin-bottom: config.sp(4);
  }

  &__product {
    display: flex;
    gap: config.sp(2.5);
    margin-top: config.sp-vertical(2);
    margin-bottom: config.sp(5);
  }

  &__productThumbnail {
    width: config.sp(10);
  }

  &__productInfo {
    display: flex;
    flex-direction: column;
  }

  &__productDetails {
    display: flex;
    flex-direction: column;
    gap: config.sp-vertical(1);
    margin-top: config.sp(2);
  }

  &__actions {
    display: flex;
    gap: config.sp(3);
    flex-direction: column;

    @include utility.breakpoint(config.$bp-desktop) {
      flex-direction: row;
    }
  }
}
