@use 'config';
@use 'utility';

.nc-htmlDisplay {
  line-height: 1.2;

  * + h1,
  * + h2,
  * + h3,
  * + h4,
  * + h5 {
    margin-top: config.sp(5);
  }

  * + p {
    margin-top: config.sp(3);
  }

  p,
  span {
    @include utility.getFont(config.$f-paragraph);
  }

  h1 {
    @include utility.getFont(config.$f-h1);
  }

  h2 {
    @include utility.getFont(config.$f-h2);
  }

  h3 {
    @include utility.getFont(config.$f-h3);
  }

  h4,
  h5 {
    @include utility.getFont(config.$f-h4);
  }

  ul,
  ol {
    @include utility.getFont(config.$f-paragraph);
  }

  ul {
    list-style: outside disc;
    margin-top: config.sp(3);
  }

  ol {
    list-style: outside decimal;
  }

  li {
    margin-left: config.sp(3);
  }

  li + li {
    margin-top: config.sp(2);
  }

  table {
    margin-top: config.sp(3);

    tr + tr {
      border-top: solid 1px config.$nc-c-primary;
    }

    tr {
      td {
        padding-bottom: config.sp(1);
        padding-top: config.sp(1);
        padding-right: config.sp(5);

        &:last-of-type {
          padding-right: 0;
        }

        > *:first-child {
          margin-top: 0;
        }
      }

      &:last-of-type {
        td {
          padding-bottom: 0;
        }
      }
    }
  }
}
