@use 'config';
@use 'utility';
@use 'common';
@use 'grid';

.nc-productDetail {
  @extend %grid;

  grid-row-gap: config.sp-vertical(5);

  &__display {
    grid-column: 1 / span 6;
    display: flex;

    @include utility.breakpoint(config.$bp-mobile-large) {
      grid-column: 1 / span 7;
    }

    @include utility.breakpoint(config.$bp-desktop) {
      grid-column: 1 / span 8;
    }
  }

  &__order {
    grid-column: 1 / span 6;

    @include utility.breakpoint(config.$bp-mobile-large) {
      grid-column: 8 / span 5;
    }

    @include utility.breakpoint(config.$bp-desktop) {
      grid-column: 9 / span 4;
    }

    &__selector {
      margin-top: config.sp-vertical(3);
    }

    &__quantity {
      margin-top: config.$gutter--default;
    }

    &__addToCart {
      margin-top: config.sp-vertical(5);
    }
  }

  &__details {
    grid-column: 1 / span 6;

    @include utility.breakpoint(config.$bp-mobile-large) {
      grid-column: 1 / span 12;
    }
  }
}
