@use 'config';
@use 'utility';

.nc-card {
  &--elevation {
    box-shadow: config.$nc-shadow-small;
    background-color: config.$nc-c-background;
    transition: box-shadow config.$transition-basic;

    &:hover {
      box-shadow: config.$nc-shadow-small--hover;
    }
  }

  &--panel {
    background-color: config.$nc-c-primary--disabled;
  }

  &--error {
    background-color: config.$nc-c-error-contrast;
  }

  &--success {
    background-color: config.$nc-c-success-contrast;
  }

  &--warning {
    background-color: config.$nc-c-warning-contrast;
  }
}
