// -- specific
@use 'styles/customer';

body {
  font-family: config.$nc-font-family;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
  font-size: 0.825rem;
  color: config.$nc-c-text;
}

h1 {
  margin: 0;
}

.main {
  @extend %container;
  padding-bottom: config.sp(7);
  min-height: 70vh;
}

// dev
nav {
  padding: 1rem 0;

  a {
    border-radius: 0.5rem;
    border: solid 1px #ccc;
    color: black;
    padding: 0.25rem 0.5rem;
    text-decoration: none;

    + a {
      margin-left: 0.5rem;
    }

    &.active {
      background-color: #a6a8ad;
    }
  }
}
