@use 'config';
@use 'utility';

.nc-header-logo {
  height: config.sp(2.8125);
  width: config.sp(11.25);
  display: flex;

  @include utility.breakpoint(config.$bp-mobile-small) {
    height: config.sp(4.5);
    width: config.sp(18);
  }

  @include utility.breakpoint(config.$bp-mobile-large) {
    height: config.sp(8.5);
    width: config.sp(25);
  }

  // fix for logo not spanning the container in safari 15
  > svg {
    width: inherit;
    height: inherit;
  }
}
