@use 'config';
@use 'utility';
@use 'common';

.nc-formControl {
  $this: &;

  & + & {
    margin-top: config.sp-vertical(3);
  }

  &--hasError {
    color: config.$nc-c-error;
  }

  &--isDisabled {
    color: config.$nc-c-primary--disabled-contrast;
    pointer-events: none;
  }

  &__label {
    @include utility.getFont(config.$f-formControlLabelVertical);

    color: inherit;
    display: block;
    margin-bottom: config.sp-vertical(1);
  }

  &__input {
    @include utility.getFont(config.$f-formControlInput);

    @extend %form-control-appearance;

    color: inherit;
    border-color: inherit;
    width: 100%;
  }

  &__helperText {
    color: inherit;
    margin-top: config.sp(1);
  }
}
