@use 'config';

.nc-pagination {
  &__pages {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: config.$gutter--default;
  }

  &__next {
    transform: rotate(180deg);
  }

  &__control {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: config.$gutter--default;
    margin-top: config.$gutter--default;

    &__meta {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      gap: config.sp(0.75);
    }

    &__pageSize {
      margin-left: auto;
    }
  }
}
