@use 'config';
@use 'utility';
@use 'grid';
@use 'common';

// TODO adapt provisional styling once the ui specification is finished
.nc-productListItem {
  @extend %grid;

  min-height: config.sp(20);
  grid-template-areas:
    'image image info info info info'
    'action action action action action action';

  @include utility.breakpoint(config.$bp-mobile-large) {
    grid-template-areas:
      'image image image image info info info info info info info info'
      'action action action action action action action action action action action action ';
  }

  @include utility.breakpoint(config.$bp-desktop) {
    grid-template-areas: 'image image info info info info action action action action action action';
  }

  & + & {
    margin-top: config.sp-vertical(3);
  }

  // layout
  &__info {
    grid-area: info;
    padding-right: config.sp(2.5);
    padding-top: config.sp-vertical(3);

    @include utility.breakpoint(config.$bp-desktop) {
      padding-right: 0;
    }
  }

  &__action {
    @extend %box-padding--card;

    display: flex;
    flex-direction: column;
    gap: config.sp(2);
    grid-area: action;
    text-align: center;

    @include utility.breakpoint(config.$bp-mobile-medium) {
      align-items: center;
      flex-direction: row;
      justify-content: space-between;
    }

    @include utility.breakpoint(config.$bp-desktop) {
      justify-content: space-around;
      padding-bottom: 0;
      padding-left: 0;
      padding-top: 0;
    }
  }

  // inner components
  &__image {
    grid-area: image;
    padding: config.sp(2);
  }
  &__price {
    margin-top: config.sp-vertical(2);
  }
  &__link {
    @include utility.getFont(config.$f-link);
  }
  &__order {
    flex-shrink: 0;
  }
}
