@use 'config';
@use 'common';
@use 'utility';

.nc-customer {
  &__account {
    &__header {
      > * + * {
        margin-top: config.sp-vertical(2);
      }

      @include utility.breakpoint(config.$bp-mobile-medium) {
        display: flex;
        align-items: center;
        justify-content: space-between;

        > * + * {
          margin-top: unset;
        }
      }
    }
  }

  &__addressSelect {
    display: flex;
    flex-direction: column;
    gap: config.$gutter--default;
    width: 100%;

    &__control {
      align-items: flex-start;
      cursor: pointer;
      flex-shrink: 0;
    }
  }

  &__addressDisplay {
    @extend %box-padding--default;

    border: solid 1px config.$nc-c-secondary;
    border-radius: 6px;

    &__defaults {
      display: flex;
      gap: config.$gutter--default;
      margin-top: config.sp-vertical(2);
    }
  }

  &__addressDefaults {
    gap: config.$gutter--default;
    display: flex;
    flex-wrap: wrap;

    @include utility.breakpoint(config.$bp-mobile-large) {
      width: 60%;
    }
  }

  &__orders {
    &__search {
      max-width: config.sp(50);
      min-width: config.sp(32);
    }
    &__list {
      margin-top: config.sp-vertical(3);
    }
  }

  &__orderItem {
    @extend %box-padding--default;

    & + & {
      margin-top: config.$gutter--default;
    }

    &__meta {
      display: flex;
      justify-content: space-between;
      margin-bottom: config.sp-vertical(2);
    }

    &__items {
      display: flex;
      gap: config.$gutter--default;
      flex-wrap: wrap;
    }

    &__item {
      margin-top: config.sp-vertical(2);
      flex-basis: 33%;
    }

    &__itemOptions {
      margin-top: config.sp-vertical(2);
    }
  }
}
