@use 'config';
@use 'utility';
@use 'grid';

// TODO this adhoc styling is provisionally --> clarify UX/UI specification and apply
.nc-stage {
  @extend %grid;

  &__header {
    grid-column: span 6;
    padding-bottom: config.sp-vertical(5);

    @include utility.breakpoint(config.$bp-mobile-large) {
      grid-column: span 12;
    }

    @include utility.breakpoint(config.$bp-desktop) {
      grid-column: span 8;
    }

    &__description {
      margin-top: config.sp-vertical(3);
    }
  }

  &__action {
    grid-column: span 6;
    padding-top: config.sp-vertical(5);
    padding-bottom: config.sp-vertical(5);
    text-align: right;

    @include utility.breakpoint(config.$bp-mobile-medium) {
      grid-column: span 12;
    }
  }
}
