@use 'config';
@use 'utility';

.nc-labelList {
  $this: &;

  & + & {
    margin-top: config.$gutter--default;
  }

  &__item {
    align-items: center;
    display: flex;
    gap: config.$gutter--default;
    justify-content: space-between;
    min-height: config.sp(3);
    width: 100%;

    & + & {
      margin-top: config.$gutter--default;
    }

    #{$this}--dense & + & {
      margin-top: config.sp-vertical(1);
    }
  }

  &__label {
    @include utility.getFont(config.$f-labelList);

    flex: 0 1 50%;
    font-weight: 700;
  }

  &__content {
    @include utility.getFont(config.$f-labelList);

    flex: 0 1 50%;
    text-align: right;
  }
}
