@use 'config';
@use 'utility';

.nc-formControlLabel {
  $this: &;

  align-items: center;
  color: config.$nc-c-primary;
  cursor: pointer;
  display: flex;
  min-height: config.sp(3);
  position: relative;

  &:hover {
    color: config.$nc-c-primary--hover;
  }

  &--isDisabled {
    color: config.$nc-c-primary--disabled-contrast;
    pointer-events: none;
  }

  &--isDisplayOnly {
    cursor: default;
    pointer-events: none;
  }

  &__label {
    @include utility.getFont(config.$f-formControlLabelHorizontal);

    flex-grow: 1;
    margin-left: config.sp(1.5);
    transition: color config.$transition-basic;
  }

  &__input {
    appearance: none;
  }

  &__control {
    $control: &;

    align-items: center;
    background-color: config.$nc-c-transparent;
    border: 1px solid currentColor;
    display: flex;
    flex-shrink: 0;
    font-size: config.sp(3);
    height: config.sp(3);
    justify-content: center;
    transition: color config.$transition-basic;
    width: config.sp(3);
    z-index: 1;

    &--checkbox {
      border-radius: config.sp(0.5);

      #{$control}__icon {
        color: config.$nc-c-white;
        font-size: config.sp(3);
        visibility: hidden;
        z-index: 2;
      }
    }

    &--radio {
      border-radius: 100%;
      position: relative;
    }

    #{$this}__input:checked + & {
      &--checkbox {
        background-color: currentColor;
        border-color: currentColor;

        #{$control}__icon {
          visibility: visible;
        }
      }

      &--radio {
        border-width: config.sp(1);
      }
    }
  }

  &__input:focus-visible + &__control {
    box-shadow: config.$nc-shadow--focus;
  }
}
