@use 'config';
@use 'utility';

strong,
b {
  font-weight: 700;
}

a {
  text-decoration: underline;
  color: inherit;
}

.nc-typography {
  $this: &;

  line-height: 1.2;

  & + &--autoMargin {
    margin-top: 0.6em;
  }

  &--h1 {
    @include utility.getFont(config.$f-h1);
  }

  &--h2 {
    @include utility.getFont(config.$f-h2);
  }

  &--h3 {
    @include utility.getFont(config.$f-h3);
  }

  &--h4 {
    @include utility.getFont(config.$f-h4);
  }

  &--paragraph {
    @include utility.getFont(config.$f-paragraph);
  }

  &--meta {
    @include utility.getFont(config.$f-meta);
  }

  &--navigation {
    @include utility.getFont(config.$f-navigation);
  }

  &__startAdornment {
    display: inline-block;
    float: left;
    height: 1.2em;
    margin-right: config.sp(1.5);
    width: 1.2em;
  }
}
