@use 'config.breakpoints';

// typography
$nc-font-family: 'Gellix', 'Arial', sans-serif;
$nc-base-font-size: 16; // unitless, used for calculation purpose only!
// -- typography variants responsive maps
$f-h1: (
  default: (
    font-size: 48,
    font-weight: 300,
  ),
  config.$bp-mobile-large: (
    font-size: 60,
  ),
);
$f-h2: (
  default: (
    font-size: 32,
    font-weight: 300,
  ),
  config.$bp-mobile-large: (
    font-size: 48,
  ),
);
$f-h3: (
  default: (
    font-size: 24,
    font-weight: 400,
  ),
  config.$bp-mobile-large: (
    font-size: 32,
  ),
);
$f-h4: (
  default: (
    font-size: 18,
    font-weight: 300,
  ),
  config.$bp-mobile-large: (
    font-size: 24,
  ),
);
$f-paragraph: (
  default: (
    font-size: 16,
    font-weight: 400,
  ),
);
$f-meta: (
  default: (
    font-size: 12,
    font-weight: 400,
  ),
  config.$bp-mobile-large: (
    font-size: 14,
  ),
);
$f-navigation: (
  default: (
    font-size: 14,
    font-weight: 400,
  ),
  config.$bp-mobile-large: (
    font-size: 18,
  ),
);

// -- components responsive maps
$f-avatar: (
  default: (
    font-size: 12,
    font-weight: 400,
  ),
  config.$bp-mobile-large: (
    font-size: 24,
  ),
);
$f-button: (
  default: (
    font-size: 18,
    font-weight: 600,
  ),
);
$f-text-button: (
  default: (
    font-size: 18,
    font-weight: 600,
  ),
);
$f-link: (
  default: (
    font-size: 18,
  ),
);
$f-labelList: (
  default: (
    font-size: 16,
  ),
);
$f-formControlLabelHorizontal: (
  default: (
    font-size: 18,
    font-weight: 400,
  ),
);
$f-formControlLabelVertical: (
  default: (
    font-size: 16,
    font-weight: 600,
  ),
);
$f-formControlInput: (
  default: (
    font-size: 18,
    font-weight: 400,
    line-height: 1.3,
  ),
);
$f-widgetCount: (
  default: (
    font-size: 10,
    font-weight: 600,
  ),
  config.$bp-mobile-large: (
    font-size: 13,
  ),
);
$f-stepperLabel: (
  default: (
    font-size: 24,
    font-weight: 600,
  ),
);
$f-disclaimer: (
  default: (
    font-size: 12,
    font-weight: 400,
  ),
);
