@use 'config';
@use 'utility';
@use 'grid';
@use 'common';

.nc-checkout {
  @extend %grid;

  grid-row-gap: config.sp-vertical(3);

  &--standalone {
    margin-top: config.sp-vertical(5);
  }

  &__main {
    grid-column: 1 / span 6;

    @include utility.breakpoint(config.$bp-mobile-large) {
      grid-column: 1 / span 12;
    }
    @include utility.breakpoint(config.$bp-desktop) {
      grid-column: 1 / span 8;
    }

    &:not(&--summary):not(&--confirmation) {
      @extend %box-padding--default;
    }
  }

  &__aside {
    grid-column: 1 / span 6;

    @include utility.breakpoint(config.$bp-mobile-large) {
      grid-column: 1 / span 12;
    }
    @include utility.breakpoint(config.$bp-desktop) {
      grid-column: 9 / span 4;
    }
  }

  &__section {
    & + & {
      margin-top: config.sp-vertical(5);
    }

    &__header {
      display: flex;
      align-items: center;
      margin-bottom: config.sp-vertical(2);
      gap: config.$gutter--default;
    }

    &__content {
      > * + * {
        margin-top: config.$gutter--default;
      }

      &--dense {
        @extend %dense-box;
      }
    }

    &__action {
      display: flex;
      align-items: center;
      margin-left: auto;
    }

    // TODO consider dedicated button group
    &__actionGroup {
      display: flex;
      align-items: center;

      > * + * {
        margin-left: config.sp(1);
      }
    }
  }

  &__shipping {
    &__addressForm {
      margin-top: config.sp(2);
    }

    &__modalAddress {
      margin-top: config.sp(2);
    }

    &__modalAction {
      margin-top: config.sp(4);
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__confirmationCard {
    display: flex;

    & + & {
      margin-top: config.sp-vertical(3);
    }

    &--display {
      padding: config.sp-vertical(3) config.sp-vertical(2) config.sp-vertical(4);

      > * {
        flex-grow: 1;
      }
    }

    &__edit {
      padding: config.sp-vertical(3) config.sp-vertical(2) 0;
    }
  }

  &__finale {
    margin-top: config.sp(5);
    padding: config.sp(5);
    display: flex;

    &__sun {
      align-self: flex-start;
      flex: 0 0 config.sp(11);
      margin-right: config.sp(5);
    }

    &__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &:first-child {
        flex-grow: 1;
      }
    }
  }
}
