@use 'config';
@use 'utility';
@use 'common';

.nc-cart {
  &__list {
    &__footer {
      align-items: center;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      margin-top: config.$gutter--default;

      > * + * {
        margin-top: config.sp-vertical(2);
      }
    }
  }

  &__summary {
    &__voucher {
      @extend %box-padding--default;

      > * + * {
        margin-top: config.sp-vertical(2);
      }
    }
    &__invoice {
      @extend %box-padding--default;
    }
    &__action {
      margin-top: config.$gutter--default;
    }
    &__legal {
      margin-top: config.$gutter--default;
    }
  }
}
