@use 'config';
@use 'utility';
@use 'common';
@use 'grid';

.nc-notificationHub {
  &__item {
    animation: {
      iteration-count: 1;
      fill-mode: forwards;
    }
    position: fixed;
    top: config.sp(13);
    transform: translateY(0);
    width: 100vw;
    z-index: config.z-index(notification);

    &.isActive {
      animation: {
        name: notificationItemAppear;
        timing-function: ease-out;
      }
    }

    &.isGoing {
      animation: {
        name: notificationItemDisappear;
        timing-function: ease-in;
      }
    }
  }

  &__grid {
    @extend %container;
    @extend %grid;
  }

  &__content {
    align-items: flex-end;
    display: flex;
    flex-direction: column;
    gap: config.sp-vertical(3);
    grid-column: 1 / span 6;
    padding: config.sp-vertical(3) 0;

    @include utility.breakpoint(config.$bp-mobile-large) {
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
      grid-column: 1 / span 12;
    }

    @include utility.breakpoint(config.$bp-desktop) {
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
      grid-column: 2 / span 10;
    }
  }

  &__message {
    display: flex;
    gap: config.sp(2);
    align-items: center;
  }

  &__dismiss {
    @extend %button-appearance;

    &:hover {
      background-color: rgba(white, 0.5);
    }
  }
}

@keyframes notificationItemAppear {
  from {
    transform: translateY(-120%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes notificationItemDisappear {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(-120%);
    opacity: 0;
  }
}
